export default "() => {\n  const [startDate, setStartDate] = useState(new Date());\n  return (\n    <DatePicker\n      selected={startDate}\n      onChange={(date) => setStartDate(date)}\n      popperClassName=\"some-custom-class\"\n      popperPlacement=\"top-end\"\n      popperModifiers={[\n        {\n          name: \"offset\",\n          options: {\n            offset: [5, 10],\n          },\n        },\n        {\n          name: \"preventOverflow\",\n          options: {\n            rootBoundary: \"viewport\",\n            tether: false,\n            altAxis: true,\n          },\n        },\n      ]}\n    />\n  );\n};\n";